function borderRadiusGenerator() {
  let topLeft = document.getElementById("top-left").value;
  let topright = document.getElementById("top-right").value;
  let bottomLeft = document.getElementById("bottom-left").value;
  let bottomRight = document.getElementById("bottom-right").value;

  let TopleftCount = document.getElementById("TopleftCount");
  TopleftCount.innerHTML = topLeft;
  let ToprightCount = document.getElementById("ToprightCount");
  ToprightCount.innerHTML = topright;
  let bottomleftCount = document.getElementById("bottomleftCount");
  bottomleftCount.innerHTML = bottomLeft;
  let bottomrightCount = document.getElementById("bottomrightCount");
  bottomrightCount.innerHTML = bottomRight;

  let resultBorder = document.getElementById("resultBorder");
  let borderRadiusCSS = document.getElementById("borderRadiusCSS");

  let brTmp;
  let btns = document.querySelectorAll(".btnItem");

  for (let btn of btns) {
    btn.onclick = function () {
      document
        .querySelector(".btn-label .active")
        .classList.remove("active");
      this.classList.add("active");

      if (document.querySelector(".active").innerHTML === "Pixels") {
        brTmp = `border-radius: ${topLeft}px ${topright}px ${bottomLeft}px ${bottomRight}px;`;
      } else {
        brTmp = `border-radius: ${topLeft}% ${topright}% ${bottomLeft}% ${bottomRight}%;`;
      }
      console.log(brTmp);
      resultBorder.style = brTmp;
      borderRadiusCSS.value = brTmp;
    };
  }

  if (document.querySelector(".active").innerHTML === "Pixels") {
    brTmp = `border-radius: ${topLeft}px ${topright}px ${bottomLeft}px ${bottomRight}px;`;
  } else {
    brTmp = `border-radius: ${topLeft}% ${topright}% ${bottomLeft}% ${bottomRight}%;`;
  }

  resultBorder.style = brTmp;
  borderRadiusCSS.value = brTmp;
}
borderRadiusGenerator();
for (let ww of document.querySelectorAll("#borderRadiusForm input")) {
  ww.addEventListener("input", borderRadiusGenerator);
}

function testScale() {
  let scaleCount = document.querySelector(".scaleCount");
  scaleCount.innerHTML = document.getElementById("scale").value;
  let rotateCount = document.getElementById("rotateCount");
  rotateCount.innerHTML = document.getElementById("rotate").value;
  let translateXCount = document.getElementById("translateXCount");
  translateXCount.innerHTML = document.getElementById("translateX").value;
  let translateYCount = document.getElementById("translateYCount");
  translateYCount.innerHTML = document.getElementById("translateY").value;
  let skewXCount = document.getElementById("skewXCount");
  skewXCount.innerHTML = document.getElementById("skewX").value;
  let skewYCount = document.getElementById("skewYCount");
  skewYCount.innerHTML = document.getElementById("skewY").value;

  let scale = document.getElementById("scale").value;
  let rotate = document.getElementById("rotate").value;
  let translateX = document.getElementById("translateX").value;
  let translateY = document.getElementById("translateY").value;
  let skewX = document.getElementById("skewX").value;
  let skewY = document.getElementById("skewY").value;

  let transformCSS = document.getElementById("transformCSS");
  let resultTransform = document.getElementById("resultTransform");

  let transStr = `-webkit-transform: scale(${scale}) rotate(${rotate}deg) translate(${translateX}px, ${translateY}px) skew(${skewX}deg, ${skewY}deg);
-moz-transform: scale(${scale}) rotate(${rotate}deg) translate(${translateX}px, ${translateY}px) skew(${skewX}deg, ${skewY}deg);
-ms-transform: scale(${scale}) rotate(${rotate}deg) translate(${translateX}px, ${translateY}px) skew(${skewX}deg, ${skewY}deg);
-o-transform: scale(${scale}) rotate(${rotate}deg) translate(${translateX}px, ${translateY}px) skew(${skewX}deg, ${skewY}deg);
transform: scale(${scale}) rotate(${rotate}deg) translate(${translateX}px, ${translateY}px) skew(${skewX}deg, ${skewY}deg);`;

  resultTransform.style = transStr;
  transformCSS.innerHTML = transStr;
}
testScale();

for (let item of document.querySelectorAll("#transformForm input")) {
  item.addEventListener("input", testScale);
}

function autoCopy(btn, place) {
  let textarea = display(document.createElement("textarea"), "none"); // создаём переменную и присваиваем ей textarea ХТМЛ элеменет который тут же и создаём и при помощи описаной ниже функции display устанавливаем ей значение none
  document.body.append(textarea); // записываем нашу переменную а вернее тег textarea в тело body

  let copy = document.getElementById(btn); // призначаем переменной copy айди кнопки для копирования
  let field = document.getElementById(place); // призначаем переменной field айди место хранения текста для копирования

  copy.addEventListener("click", function () {
    // додаём обработчик событий что при клике на кнопку для копирования выполнялась функция initCopy и передаём в неё в качестве параметра текст который храниться в переменной field
    initCopy(field.value);
  });

  function initCopy(str) {
    // функция для копирования текста
    display(textarea).value = str; // для textarea в его значение присваиваем текст который передали в качестве параметра, при этом можно сказать что у отображаеться ещё одно поле textarea поскольку значение по умолчанию для функции display:block;
    textarea.select(); // далее мы выделяем весь текст спомощу метода select()
    document.execCommand("copy"); // далее спомощу execCommand('copy') копируем текст
    display(textarea, "none"); // и делаем display:none; для textarea что бы его не было видно на странице, таким образом его как бы и нет на сайте, но он есть
  }
}

function display(elem, value) {
  // функция для показании либо скрытии элемента на сайте. передаём два параметра такие как elem - сам элемент на которые буде применяться действие и value - само значение которое будет применяться на элемент
  elem.style.display = value || "block"; // здесь просто прописываем этому элементу то значение которое было указано, если же ничего не было указано как на строке " display(textarea).value = str;" то по умолчанию примениться значение display: block
  return elem; // возращаем наш элемент
}

