function flexBoxGenerator() {
  let display = document.getElementById("displaySelect").value;
  let flexDirection = document.getElementById("fdSelect").value;
  let flexWrap = document.getElementById("fwSelect").value;
  let justifyContent = document.getElementById("jcSelect").value;
  let alignItems = document.getElementById("aiSelect").value;
  let alignContent = document.getElementById("acSelect").value;

  let flexBoxCSS = document.getElementById("flexBoxCSS");
  let mainBoxResult = document.getElementById("mainBoxResult");

  let flexBoxStr = `display: ${display};
flex-direction: ${flexDirection};
flex-wrap: ${flexWrap};
justify-content: ${justifyContent};
align-items: ${alignItems};
align-content: ${alignContent};`;
  flexBoxCSS.value = flexBoxStr;
  mainBoxResult.style = flexBoxStr;
}

flexBoxGenerator();
for (let elem of document.querySelectorAll("#flexboxForm select")) {
  elem.addEventListener("change", flexBoxGenerator);
}

let i = 4;
document
  .getElementById("addElem")
  .addEventListener("click", function (e) {
    e.preventDefault();
    let box = document.getElementById("mainBoxResult");
    let div = document.createElement("div");
    div.classList.add("resultFlexBox");
    if (i == 11) return false;
    div.innerHTML = i;
    box.append(div);
    i = i + 1;
  });

document
  .getElementById("removeElem")
  .addEventListener("click", function (e) {
    e.preventDefault();
    let parent = document.querySelector("#mainBoxResult");
    let last = parent.lastElementChild;
    if (last.innerHTML < 4) return false;
    last.remove();
    i = +last.innerHTML;
  });

