function textShadowGenerator() {
  let text = document.getElementById("text").value;
  let resultTextShadow = document.getElementById("resultTextShadow");
  resultTextShadow.innerHTML = text;

  let horizontal = document.getElementById("horizontal").value;
  let vertical = document.getElementById("vertical").value;
  let TSblur = document.getElementById("TSblur").value;

  let horizontalCount = document.getElementById("horizontalCount");
  horizontalCount.innerHTML = horizontal;

  let verticalCount = document.getElementById("verticalCount");
  verticalCount.innerHTML = vertical;

  let blurCount = document.getElementById("blurCount");
  blurCount.innerHTML = TSblur;

  let TScolor = document.getElementById("TScolor").value;
  let TSred = parseInt(TScolor.slice(1, 3), 16);
  let TSgreen = parseInt(TScolor.slice(3, 5), 16);
  let TSblue = parseInt(TScolor.slice(5, 7), 16);

  let TSStr = `text-shadow: ${horizontal}px ${vertical}px ${TSblur}px rgba(${TSred}, ${TSgreen}, ${TSblue}, 1);`;

  let textShadowCSS = document.getElementById("textShadowCSS");
  textShadowCSS.value = TSStr;
  resultTextShadow.style = TSStr;
}
textShadowGenerator();
for (let item of document.querySelectorAll("#text-shadowForm input")) {
  item.addEventListener("input", textShadowGenerator);
}


function transitionGenerator() {
  let duration = document.getElementById("duration").value;
  let durationCount = document.getElementById("durationCount");
  durationCount.innerHTML = duration;

  let propertySelect = document.getElementById("propertySelect").value;
  let linearProperty = document.getElementById("linearProperty").value;

  let transitionCSS = document.getElementById("transitionCSS");
  let resultTransition = document.getElementById("resultTransition");

  let strtrans = `-webkit-transition: ${propertySelect} ${duration}s ${linearProperty};
-moz-transition: ${propertySelect} ${duration}s ${linearProperty};
-ms-transition: ${propertySelect} ${duration}s ${linearProperty};
-o-transition: ${propertySelect} ${duration}s ${linearProperty};
transition: ${propertySelect} ${duration}s ${linearProperty};`;

  transitionCSS.value = strtrans;

  let tmp;

  switch (propertySelect) {
    case "all":
      tmp = `background: #1f2024;
      color: #5983fc;
      width: 320px;
      box-shadow: 4px 5px 8px 8px rgba(0, 0, 0, 0.5);
      transform: rotate(15deg);
      border-radius: 0px;
      transition: ${propertySelect} ${duration}s ${linearProperty};
      border: 5px dashed  hsl(225, 96%, 67%);
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;
    case "background":
      tmp = `background: #1f2024;
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;
    case "color":
      tmp = `color: #1f2024;
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;
      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;
    case "width":
      tmp = `
      width: 320px;
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;

    case "opacity":
      tmp = `
      opacity: 0.3;
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;
    case "box-shadow":
      tmp = `
      box-shadow: 4px 5px 8px 8px rgba(0, 0, 0, 0.5);
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;

    case "transform":
      tmp = `
      transform: rotate(15deg);
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;
    case "border-radius":
      tmp = `
      border-radius: 0px;
      transition: ${propertySelect} ${duration}s ${linearProperty};
      `;

      resultTransition.addEventListener("mouseover", function () {
        this.style = tmp;
      });
      resultTransition.addEventListener("mouseout", function () {
        this.style = `transition: ${propertySelect} ${duration}s ${linearProperty};`;
      });
      break;
  }
}

transitionGenerator();
for (let item of document.querySelectorAll("#transitionForm input")) {
  item.addEventListener("input", transitionGenerator);
}
for (let elem of document.querySelectorAll("#transitionForm select")) {
  elem.addEventListener("change", transitionGenerator);
}